import { BigNumberish } from 'ethers';

import { JsonRpcError, JsonRpcRequestPayload, JsonRpcResponsePayload } from './json-rpc';

export enum FaceMessageType {
  FACE_LOG_IN_SIGN_UP = 'FACE_LOG_IN_SIGN_UP',
  FACE_LOGGED_IN = 'FACE_LOGGED_IN',
  FACE_SEND_TRANSACTION = 'FACE_SEND_TRANSACTION',
  FACE_ACCOUNTS = 'FACE_ACCOUNTS',
  FACE_RPC = 'FACE_RPC',
  FACE_LOG_OUT = 'FACE_LOG_OUT',
  FACE_READY = 'FACE_READY',
  FACE_CLOSE_IFRAME = 'FACE_CLOSE_IFRAME',
  FACE_OPEN_IFRAME = 'FACE_OPEN_IFRAME',
  FACE_CURRENT_USER = 'FACE_CURRENT_USER',
}

export interface FaceTransactionPayload {
  from: string;
  to: string;
  amount?: BigNumberish;
  data?: string;
}

export interface FaceLoginResponse {
  faceUserId: string;
  name: string;
  email: string;
}

export interface FaceMessageRequest {
  type: FaceMessageType; // TODO: generic payload
  timestamp?: number;
  transactionPayload?: FaceTransactionPayload;
  rpcPayload?: JsonRpcRequestPayload | JsonRpcRequestPayload[];
  rt?: string;
  jwt?: string;
}

export type AccountsResponse = string[];

export interface FaceMessageResponse<ResultType = any> {
  type: FaceMessageType;
  result:
    | null
    | boolean
    | FaceLoginResponse
    | AccountsResponse
    | Partial<JsonRpcResponsePayload<ResultType>>;
  timestamp?: number;
}

export interface FaceMessageEvent extends Partial<MessageEvent> {
  data: FaceMessageResponse;
}
