import { Internal } from './internal';

export interface RequestArguments {
  method: string;
  params?: any;

  [key: string]: any;
}

class ProviderTemplate {
  sendAsync = (r: any, c: (e: any, r: any) => void) => void 0;
}

export class Provider extends ProviderTemplate {
  private readonly internal: Internal;

  constructor(internal: Internal) {
    super();
    this.internal = internal;
  }

  async request(request: RequestArguments | RequestArguments[]): Promise<any> {
    if (Array.isArray(request)) {
      return this.internal.sendRpc(
        request.map((request, index) =>
          // TODO:  request.id = index;
          Object.defineProperties<any>(request, {
            jsonrpc: {
              value: '2.0',
              writable: false,
              enumerable: true,
            },
          })
        )
      );
    }

    switch (request.method) {
      case 'eth_accounts':
        return this.internal.getAddresses();
      case 'eth_estimateGas':
        return this.internal.estimateGas(request);
      case 'eth_sendTransaction':
        const params = request.params[0];
        return this.internal.sendTransaction({
          from: params.from,
          to: params.to,
          amount: params.value,
          data: params.data,
        });
      default:
        return this.internal.sendRpc(request);
    }
  }
}
